import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { AuthService } from "./../../_services";
import { User } from "./../../_models";
import { PortalService } from "../../_services";
declare var jQuery: any;

@Component({
  selector: "app-orders",
  templateUrl: "./orders.component.html",
  styleUrls: ["./orders.component.css"],
})
export class OrdersComponent implements OnInit {
  message: any;
  currentUser: User;
  orders: Array<any>;
  pendingOrders: Array<any>;
  acceptedOrders: Array<any>;
  deliveredOrders: Array<any>;
  completedOrders: Array<any>;
  status: string;
  isAdmin: boolean;

  constructor(
    private router: Router,
    private authService: AuthService,
    private portalService: PortalService
  ) {
    this.authService.currentUser.subscribe((x) => (this.currentUser = x));
  }

  ngOnInit(): void {
    this.isAdmin = this.authService.isAdmin();
    this.message = localStorage.getItem("message");
    localStorage.removeItem("message");
    this.loadOrders();
  }

  loadOrders() {
    if (!this.currentUser) {
      return;
    }
    jQuery(".ibox-content").toggleClass("sk-loading");
    this.status = "all";
    this.orders = [];
    this.pendingOrders = [];
    this.acceptedOrders = [];
    this.deliveredOrders = [];
    this.completedOrders = [];

    this.portalService.getOrders(this.currentUser).then((response) => {
      response.sort((x, y) => +new Date(y.date) - +new Date(x.date));
      response.forEach((order) => {
        switch (order.status) {
          case "pending":
            this.pendingOrders.push(order);
            break;
          case "accepted":
            this.acceptedOrders.push(order);
            break;
          case "delivered":
            this.deliveredOrders.push(order);
            break;
          case "completed":
            this.completedOrders.push(order);
            break;
          default:
            break;
        }
      });
      jQuery(".ibox-content").toggleClass("sk-loading");
      this.showOrders();
    });
  }

  showOrders(status = this.status): void {
    jQuery(".ibox-content").toggleClass("sk-loading");
    this.orders = [];
    this.status = status;
    switch (this.status) {
      case "all":
        this.orders.push(
          ...this.pendingOrders,
          ...this.acceptedOrders,
          ...this.deliveredOrders,
          ...this.completedOrders
        );
        break;
      case "pending":
        this.orders = this.pendingOrders;
        break;
      case "accepted":
        this.orders = this.acceptedOrders;
        break;
      case "delivered":
        this.orders = this.deliveredOrders;
        break;
      case "completed":
        this.orders = this.completedOrders;
        break;
      default:
        break;
    }

    jQuery(document).ready(function () {
      jQuery(".ibox-content").toggleClass("sk-loading");
      setTimeout(() => {
        jQuery(".footable").trigger("footable_initialize");
      }, 1000);
    });
  }
}
