import { Component, OnInit } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { AuthService } from "./../../_services";
declare var jQuery: any;

@Component({
  selector: "app-payment",
  templateUrl: "./payment.component.html",
  styleUrls: ["./payment.component.css"],
})
export class PaymentComponent implements OnInit {
  paymentForm: FormGroup;
  submitted = false;
  error = "";
  order: any;

  constructor(
    private formBuilder: FormBuilder,
    private authService: AuthService
  ) {}

  ngOnInit(): void {
    this.error = "";
    this.order = JSON.parse(localStorage.getItem("order"));
    this.paymentForm = this.formBuilder.group({
      name: ["Kari Karthick", [Validators.required]],
      number: [
        "4242424242424242",
        [
          Validators.required,
          Validators.minLength(16),
          Validators.maxLength(16),
        ],
      ],
      mmyyyy: [
        "112022",
        [Validators.required, Validators.minLength(6), Validators.maxLength(6)],
      ],
      cvv: ["123", [Validators.required]],
      amount: ["12.34", [Validators.required]],
    });
  }

  toggleSpinner() {
    jQuery(".ibox-content").toggleClass("sk-loading");
  }

  get f() {
    return this.paymentForm.controls;
  }

  onSubmit() {
    this.submitted = true;
    if (this.paymentForm.invalid) {
      return;
    }
    this.toggleSpinner();

    const card = {
      orderId: this.order.orderId,
      amount: this.order.amount * 100,
      name: this.f.name.value,
      number: this.f.number.value,
      expiration_month: this.f.mmyyyy.value.substring(0, 2),
      expiration_year: this.f.mmyyyy.value.substring(2),
      security_code: this.f.cvv.value,
    };

    this.authService.chargeOmise(card);

    this.toggleSpinner();

    // .then(
    //   (res) => {
    //     console.log("res ==> ", res);
    //   },
    //   (err) => {
    //     console.log("err ==> ", err);
    //     this.error = err;
    //   }
    // );
  }
}
