import { Component, OnInit } from "@angular/core";
import { PortalService } from "../../_services";

@Component({
  selector: "app-sugars",
  templateUrl: "./sugars.component.html",
  styleUrls: ["./sugars.component.css"],
})
export class SugarsComponent implements OnInit {
  message: any;
  products: Array<any>;

  constructor(private portalService: PortalService) {}

  ngOnInit(): void {
    this.message = "";
    this.products = [];
    this.portalService.getProducts("sugar").then((response) => {
      this.products = response;
    });
  }

  cartPlus(product: any) {
    this.portalService.cartPlus(product).then(() => {
      this.message = "Added to cart successfully!";
    });
  }
}
